import React, { Component } from 'react';
import './Login.css';

export class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            userId: '',
            userPwd: '',
            idIsValid: true,
            pwdIsValid: true,
        };

        this.onUserIdChanged = this.onUserIdChanged.bind(this);
        this.onUserPwdChanged = this.onUserPwdChanged.bind(this);
        this.submit = this.submit.bind(this);
    }

    onUserIdChanged(event) {
        this.setState({
            userId: event.target.value
        });
    }

    onUserPwdChanged(event) {
        this.setState({
            userPwd: event.target.value
        });
    }

    validate(userId, userPwd) {
        let isValid = true;

        if (userId == '') {
            this.setState({
                idIsValid: false
            });
            isValid = false;
        }
        else {
            this.setState({
                idIsValid: true
            });
        }        
        if (userPwd == '') {
            this.setState({
                pwdIsValid: false
            });
            isValid = false;
        }
        else {
            this.setState({
                pwdIsValid: true
            });
        }

        return isValid;
    }

    submit(event) {
        event.preventDefault();

        const userId = this.state.userId;
        const userPwd = this.state.userPwd;
        
        if (!this.validate(userId, userPwd));

        this.props.onStatusChanged('processing', ' ...');
        this.authorize(userId, userPwd);

        this.setState({
            userPwd: ''
        });
    }

    render() {
        const idIsValid = this.state.idIsValid;
        const pwdIsValid = this.state.pwdIsValid;

        return (
            <div>
                <h4 className='form-label'> </h4>
                <form onSubmit={this.submit}>
                    <div className='form-group'>
                        <label>:</label>
                        <input className={idIsValid ? 'form-control' : 'form-control validation-control'} type='text' value={this.state.userId} onChange={this.onUserIdChanged} />
                        {!idIsValid && <div className='validation-message'> </div>}
                    </div>
                    <div className='form-group'>
                        <label></label>
                        <input className={pwdIsValid ? 'form-control' : 'form-control validation-control'} type='password' value={this.state.userPwd} onChange={this.onUserPwdChanged} />
                        {!pwdIsValid && <div className='validation-message'> </div>}
                    </div>
                    <div className='form-submit'>
                        <input className='btn btn-primary' type='submit' value='' />
                    </div>
                </form>
            </div>
        );
    }

    async authorize(userId, userPwd) {
        const response = await fetch('/api/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                'userId': userId + '',
                'userPwd': userPwd + ''
            })
        });

        const data = await response.json();
        if (!response.ok) {
            console.log(data);
            this.props.onStatusChanged('failed', '    ');
            return;
        }

        const token = data['token'];
        
        this.props.onStatusChanged('succeeded', ' ');
        this.props.onTokenChanged(token);
    }
}