import React, { Component } from 'react';
import { EditWorkroom } from './EditWorkroom';
import validateResponse from './ValidateResponse';
import './ListWorkrooms.css';

export class ListWorkrooms extends Component {
    constructor(props) {
        super(props);

        this.state = {
            workrooms: null,
            workroom: null,
        };

        this.onBackBtnClicked = this.onBackBtnClicked.bind(this);
        this.onEditBtnClicked = this.onEditBtnClicked.bind(this);
        this.onDeleteBtnClicked = this.onDeleteBtnClicked.bind(this);
        this.onWorkroomReseted = this.onWorkroomReseted.bind(this);
    }

    componentDidMount() {
        this.populateWorkrooms();
    }

    onBackBtnClicked() {
        this.props.onPathChanged('/');
    }

    onEditBtnClicked(workroom) {
        this.setState({
            workroom: workroom
        });
    }

    onDeleteBtnClicked(workroom) {
        const status = window.confirm(' ?');
        if (status) {
            this.props.onStatusChanged('processing', ' ...');
            this.deleteWorkroom(workroom['uid']);
        }
    }

    onWorkroomReseted() {
        this.populateWorkrooms();
        this.setState({
            workroom: null
        });
    }

    render() {
        const workrooms = this.state.workrooms;
        const workroom = this.state.workroom;

        return (
            <div>
                <h4 className='form-label'> </h4>
                {workroom == null && <button className='btn btn-secondary back-btn' onClick={this.onBackBtnClicked}></button>}
                {workrooms == null ? <div>Now Loading...</div> :
                workroom == null ?
                <div>
                    <table id='list-workroom-table'>
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {workrooms.map((workroom, index) =>
                            <tr key={index} className={index % 2 == 0 ? 'even' : ''}>
                                <td>{workroom['name']}</td>
                                <td>
                                    <button className='btn btn-secondary' onClick={() => this.onEditBtnClicked(workroom)}></button>
                                    <button className='btn btn-danger' onClick={() => this.onDeleteBtnClicked(workroom)}></button>
                                </td>
                            </tr>
                            )}
                        </tbody>
                    </table>
                </div> :
                <EditWorkroom token={this.props.token} workroom={workroom} onStatusChanged={this.props.onStatusChanged} onWorkroomReseted={this.onWorkroomReseted} />
                }
            </div>
        );
    }

    async populateWorkrooms() {
        const response = await fetch('api/workroom', {
            method: 'GET',
            headers: {
                'Authorization': 'Bearer ' + this.props.token
            }
        });
        if (!response.ok) {
            validateResponse(response);
            this.props.onStatusChanged('failed', '  ');
            return;
        }

        const data = await response.json();
        this.setState({
            workrooms: data
        });
    }

    async deleteWorkroom(uid) {
        const response = await fetch('api/workroom', {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.props.token
            },
            body: JSON.stringify({
                'uid': uid
            })
        });
        if (!response.ok) {
            validateResponse(response);
            this.props.onStatusChanged('failed', ' ');
            return;
        }

        this.populateWorkrooms();
        this.props.onStatusChanged('succeeded', ' ');
    }
}