import React, { Component } from 'react';
import { EditData } from './EditData';
import validateResponse from './ValidateResponse';
import './ListData.css';

export class ListData extends Component {
    constructor(props) {
        super(props);

        this.state = {
            data: null,
            specData: null,
        };

        this.onBackBtnClicked = this.onBackBtnClicked.bind(this);
        this.onEditBtnClicked = this.onEditBtnClicked.bind(this);
        this.onDeleteBtnClicked = this.onDeleteBtnClicked.bind(this);
        this.onSpecDataReseted = this.onSpecDataReseted.bind(this);
    }

    componentDidMount() {
        this.populateData();
    }

    onBackBtnClicked() {
        this.props.onPathChanged('/');
    }

    onEditBtnClicked(data) {
        this.setState({
            specData: data
        });
    }

    onDeleteBtnClicked(data) {
        const status = window.confirm(' ?');
        if (status) {
            this.props.onStatusChanged('processing', ' ...');
            this.deleteData(data['uid']);
        }
    }

    onSpecDataReseted() {
        this.populateData();
        this.setState({
            specData: null
        });
    }

    render() {
        const data = this.state.data;
        const specData = this.state.specData;

        return (
            <div>
                <h4 className='form-label'> </h4>
                {specData == null && <button className='btn btn-secondary back-btn' onClick={this.onBackBtnClicked}></button>}
                {data == null ? <div>Now Loading...</div> :
                specData == null ?                
                <div>
                    <table id='edit-data-table'>
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th className='weight'>(kg)</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {data.map((data, index) =>
                            <tr key={index} className={index % 2 == 0 ? 'even' : ''}>
                                <td>{data['date']}</td>
                                <td>{data['workroom']}</td>
                                <td className='weight' >{data['weight'].toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",")}</td>
                                <td>
                                    <button className='btn btn-secondary' onClick={() => this.onEditBtnClicked(data)}></button>
                                    <button className='btn btn-danger' onClick={() => this.onDeleteBtnClicked(data)}></button>
                                </td>
                            </tr>
                            )}
                        </tbody>
                    </table>
                </div> :
                <EditData token={this.props.token} data={specData} onSpecDataReseted={this.onSpecDataReseted} onStatusChanged={this.props.onStatusChanged} />
                }
                <br />
                <br />
            </div>
        );
    }

    async populateData() {
        const response = await fetch('api/steel', {
            method: 'GET',
            headers: {
                'Authorization': 'Bearer ' + this.props.token
            }
        });
        if (!response.ok) {
            validateResponse(response);
            this.props.onStatusChanged('failed', '  ');
            return;
        }

        const data = await response.json();
        this.setState({
            data: data
        });
    }

    async deleteData(uid) {
        const response = await fetch('api/steel', {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.props.token
            },
            body: JSON.stringify({
                'uid': uid
            })
        });
        if (!response.ok) {
            validateResponse(response);
            this.props.onStatusChanged('failed', ' ');
            return;
        }

        this.populateData();
        this.props.onStatusChanged('succeeded', ' ');
    }
}