import React, { Component } from 'react';
import { Collapse, Container, Navbar, NavbarBrand, NavbarToggler, NavItem, NavLink } from 'reactstrap';
import { Link } from 'react-router-dom';
import { tokenName, role, adminRole, userRole, exp, expDiff } from './config';
import './NavMenu.css';

export class NavMenu extends Component {
    constructor(props) {
        super(props);

        this.toggleNavbar = this.toggleNavbar.bind(this);
        this.state = {
            collapsed: true
        };
    }

    toggleNavbar() {
        this.setState({
            collapsed: !this.state.collapsed
        });
    }

    render() {
        return (
            <header>
                <Navbar className='navbar-expand-sm navbar-toggleable-sm ng-white border-bottom box-shadow mb-3' light>
                    <Container>
                        <NavbarBrand></NavbarBrand>
                        <NavbarToggler onClick={this.toggleNavbar} className='mr-2' />
                        <Collapse className="d-sm-inline-flex flex-sm-row-reverse" isOpen={!this.state.collapsed} navbar>
                            {this.props.token != null &&
                            <ul className='navbar-nav flex-glow'>
                                <NavItem>
                                    <NavLink tag={Link} className="text-primary" to="/"></NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink tag={Link} className="text-primary" to="/chart"></NavLink>
                                </NavItem>
                                {this.props.isAdmin == adminRole &&
                                <NavItem>
                                    <NavLink tag={Link} className="text-primary" to="/admin"></NavLink>
                                </NavItem>
                                }
                                <NavItem>
                                    <NavLink tag={Link} className="text-primary" to="/logout"></NavLink>
                                </NavItem>
                            </ul>
                            }
                        </Collapse>
                    </Container>
                </Navbar>
            </header>
        );
    }
}